* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  font: 100%/1.5rem 'Times New Roman', Arial, Helvetica, sans-serif;
  color: #000;
}

/*можно указать область холста*/

.container {
  width: 100%;
  margin: 0 auto;
  /*background-color: #fd4;*/
}

/*правила для размещения колонок в один ряд и отступы между рядами*/

.container .cols {
  float: left;
  margin: 0 0 1em;
  /*влияет на отступы между колонками*/
  padding: 0 1em;
  /*влияет на внутренние отступы внутри колонки*/
  /*background-color: #fd4;*/
  /* для визуализации колонок*/
  /*text-align: center;*/
}

/* .row - обертка для !каждой! строки, чтобы удалить обтекание*/

.container .cols.col-1 {
  width: 8.333333333333333%;
}

.container .cols.col-2 {
  width: 16.666666666666667%;
}

.container .cols.col-3 {
  width: 25%;
}

.container .cols.col-4 {
  width: 33.333333333333%;
}

.container .cols.col-5 {
  width: 41.6666666666667%;
}

.container .cols.col-6 {
  width: 50%;
}

.container .cols.col-7 {
  width: 58.333333333333333%;
}

.container .cols.col-8 {
  width: 66.6666666666666667%;
}

.container .cols.col-9 {
  width: 75%;
}

.container .cols.col-10 {
  width: 83.333333333333333%;
}

.container .cols.col-11 {
  width: 91.666666666666667%;
}

.container .cols.col-12 {
  width: 100%;
}

/*clearFix для очищения флоатов*/

.container:before,
.conteiner:after,
.row:after,
.row:before,
.clear:before,
.clear:after {
  content: ' ';
  display: table;
}

.container:after,
.row:after,
.clear:after {
  clear: both;
}

/* Wrapper для рабочей области */

.wrapper {
  width: 100%;
  margin: 0 auto;
  max-width: 1200px;
}

/* Outline для визуализации колонок */

.outline {
  outline: 1px solid #15FFFC;
}

/* Responsive images */

img {
  max-width: 100%;
  height: auto;
}

/* Start header */

.wrapper {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

header.head {
  padding: 40px 0 80px 0;
}

header.head .wrapper {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

header.head p {
  font-size: 1.5rem;
  color: #010101;
}

header.head i {
  width: 17px;
  height: 17px;
  background-image: url(../img/phone_ico.png);
  margin-left: auto;
  margin-right: .55%;
}

main.first_screen {
  padding: 140px 0 130px 0;
  background: #fff url(../img/1.png) no-repeat 300px 0;
  -webkit-background-size: 100%;
  background-size: 100%;
}

main.first_screen h1 {
  display: inline-block;
  margin: 0;
  padding: 3px 10px;
  font-size: 3.75rem;
  color: #010101;
  text-align: left;
  background-color: #fff;
  line-height: 4rem;
}

main.first_screen h6 {
  color: #12e0b7;
  background-color: #fff;
  font-size: 1.125rem;
  margin-top: 5px;
  margin-left: 10%;
  width: 50%;
}